import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
const vuexLocalhost = new VuexPersistence({
    storage:window.localStorage
})

Vue.use(Vuex)


export default new Vuex.Store({
    plugins:[vuexLocalhost.plugin],
    state:{
        obj:{},
        info:[]
    },
    mutations:{
        login(state,val){
            state.obj=val
        },
        Pay(state,val){
            state.info=val
        },
        sbb(state){
            state.obj=''
        },
        gaiImg(state,val){
            state.img=val
        }
    }
})


