import Vue from 'vue'
import Router from 'vue-router'
import App from '../App.vue'
const List = resolve => require(['../page/city/List.vue'],resolve)
const Search = resolve => require(['../page/Search/Search.vue'],resolve)
const Goods = resolve => require(['../page/Good/Goods.vue'],resolve)
const Register = resolve => require(['../page/Register/Register.vue'],resolve)
const Login = resolve => require(['../page/Register/Login.vue'],resolve)
const Header = resolve => require(['../page/Head/Header.vue'],resolve)
const Cart = resolve => require(['../page/cart/cart.vue'],resolve)
const Sca = resolve => require(['../page/Search/Searchs.vue'],resolve)
const My = resolve => require(['../page/My/My.vue'],resolve)
const ShopCart = resolve => require(['../page/cart/shopCart.vue'],resolve)
const Address = resolve => require(['../page/My/Address.vue'],resolve)
const Tui = resolve => require(['../page/My/Tui.vue'],resolve)
const Sea = resolve => require(['../page/My/Sea.vue'],resolve)
const Xiang = resolve => require(['../page/cart/Xiang.vue'],resolve)
const Pay = resolve => require(['../page/cart/Pay.vue'],resolve)
const Payment = resolve => require(['../page/cart/Payment.vue'],resolve)
const Product = resolve => require(['../page/cart/Product.vue'],resolve)

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name:'App',
      component: App,
      children:[
        {
          path: '/List',
          name: 'List',
          component: List
        },
        {
          path: '/Search:id',
          name: 'Search',
          component: Search
        },
        {
          path: '/Goods:geohash',
          name: 'Goods',
          component: Goods
        },
        {
          path: '/Register',
          name: 'Register',
          component: Register
        },
        {
          path: '/Login',
          name: 'Login',
          component: Login
        },
        {
          path: '/Header',
          name: 'Header',
          component: Header
        },
        {
          path: '/Cart/:geohash/:id',
          name: 'Cart',
          component: Cart
        },
        {
          path: '/Sca:geohash',
          name: 'Sca',
          component: Sca
        },
        {
          path: '/My',
          name: 'My',
          component: My
        },
        {
          path: '/ShopCart',
          name: 'ShopCart',
          component: ShopCart
        },
        {
          path: '/Address',
          name: 'Address',
          component: Address
        },
        {
          path: '/Tui',
          name: 'Tui',
          component: Tui
        },
        {
          path: '/Sea',
          name: 'Sea',
          component: Sea
        },
        {
          path: '/Xiang:item',
          name: 'Xiang',
          component: Xiang
        },
        {
          path: '/Pay',
          name: 'Pay',
          component: Pay
        },
        {
          path: '/Payment',
          name: 'Payment',
          component: Payment
        },
        {
          path: '/Product',
          name: 'Product',
          component: Product
        }
      ]
    },
    
  ]
})



